// typeguards
export let isCommon = (obj) => !!(obj.id && obj.name);
export let isVideo = (obj) => !!obj.formats;
export let isMovie = (video) => { return !!video.icon; };
export let isShow = (obj) => !!obj.seasons;
export let isSeason = (obj) => !!obj.episodes;
export let isEpisode = (video) => { return !!video.parent; };
