// set up svt stores
export let cfg = writable();
export let tv = writable();
export let movies = writable();
export let embeddables = writable();
export let ready = writable(false);
export let IDIndex = new Map();
// fetch cfg; tv; movies
// might DRY up this code later
fetch("/db/webtv.json", { cache: "no-store" }).then(res => {
    if (res.status == 200)
        res.json().then(e => cfg.set(e));
})
    .then(() => fetch("/db/tv.json", { cache: "no-store" }).then(res => {
    if (res.status == 200)
        res.json().then((e) => {
            tv.set(e);
            e.forEach(v => {
                IDIndex.set(v.id, v);
                v.seasons.forEach(a => {
                    IDIndex.set(a.id, a);
                    a.episodes.forEach(b => IDIndex.set(b.id, b));
                });
            });
        });
}))
    .then(() => fetch("/db/movie.json", { cache: "no-store" }).then(res => {
    if (res.status == 200)
        res.json().then((e) => {
            movies.set(e);
            e.forEach(v => {
                IDIndex.set("movie." + v.id, v);
            });
        });
}))
    .then(() => fetch("/db/embeddables.json", { cache: "no-store" }).then(res => {
    if (res.status == 200)
        res.json().then(e => embeddables.set(e));
})).then(() => {
    ready.set(true);
});
